import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdListLayoutComponent } from './layouts/ad-list-layout/ad-list-layout.component';

const routes: Routes = [
	{
		path: '',
		component: AdListLayoutComponent,
		children: [
			{
				path: '',
				loadChildren: () => import('./pages/ad-list/ad-list.module').then((m) => m.AdListPageModule),
			},
			{
				path: ':stand',
				loadChildren: () => import('./pages/ad-list/ad-list.module').then((m) => m.AdListPageModule),
			},
			{
				path: ':stand/:shelf',
				loadChildren: () => import('./pages/ad-list/ad-list.module').then((m) => m.AdListPageModule),
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class AdsRoutingModule {}
