import {
	AfterViewInit,
	ChangeDetectorRef,
	Component,
	ComponentRef,
	EventEmitter,
	Input,
	OnChanges,
	Output,
	SimpleChanges,
	Type,
	ViewChild,
	ViewContainerRef,
} from '@angular/core';
import { FilterTemplateType } from '@core/enums';
import { Filter } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';
import { Subject, takeUntil } from 'rxjs';
import { AbstractControlBaseComponent } from '../abstracts/control-base.abstract';
import { FiltersButtonSelectionComponent } from './filters-button-selection/filters-button-selection.component';
import { FiltersDropdownSelectionComponent } from './filters-dropdown-selection/filters-dropdown-selection.component';
import { FiltersListSelectionComponent } from './filters-list-selection/filters-list-selection.component';
import { FiltersMinMaxComponent } from './filters-min-max/filters-min-max.component';

@Component({
    selector: 'findqo-filters-control',
    templateUrl: './filters-control.component.html',
    styleUrls: ['./filters-control.component.scss'],
    standalone: false
})
export class FiltersControlComponent<T> implements AfterViewInit, OnChanges {
	constructor(private readonly cdr: ChangeDetectorRef) {}

	@Input() filter!: Filter;
	@Input() filterTemplateType?: FilterTemplateType;
	@Output() changed = new EventEmitter<Filter>();

	@ViewChild('controlContainer', { read: ViewContainerRef }) controlContainer: ViewContainerRef;

	/**
	 * TODO: Checked if we still need these declarations
	 */
	@Input() isSelectAllControl = false;
	@Input() isMoreFilters = false;
	// end - To be checked

	componentRef: ComponentRef<T>;
	selectedOption?: string;

	readonly templateMapper = {
		[FilterTemplateType.MULTI_SELECTION]: FiltersListSelectionComponent,
		[FilterTemplateType.SINGLE_SELECTION]: FiltersListSelectionComponent,
		[FilterTemplateType.BOOLEAN_SELECTION]: FiltersListSelectionComponent,
		[FilterTemplateType.BUTTON_SELECTION]: FiltersButtonSelectionComponent,
		[FilterTemplateType.SINGLE_DROPDOWN_SELECTION]: FiltersDropdownSelectionComponent,
		[FilterTemplateType.MIN_MAX_INPUT]: FiltersMinMaxComponent,
		[FilterTemplateType.MIN_MAX_DROPDOWN]: FiltersMinMaxComponent,
	};

	private createControlContainer(filter: Filter): void {
		const COMPONENT_INSTANCE = this.getComponentType(filter);

		this.controlContainer.clear();
		this.componentRef = this.controlContainer.createComponent(COMPONENT_INSTANCE) as ComponentRef<T>;

		// this.setInput(this.componentRef);
		this.setOutput(this.componentRef);

		(<AbstractControlBaseComponent>this.componentRef.instance).filter = this.filter;

		this.cdr.detectChanges();
	}

	// private getComponentByType<T>(control: DynamicFormControl): Type<T> {
	// 	let component: any;

	// 	if (control.isMinMax) {
	// 		component = this.isMoreFilters ? FiltersMoreInputMinMaxComponent : FiltersInputMinMaxComponent;
	// 	} else {
	// 		component = this.isMoreFilters ? FiltersMoreSelectComponent : FiltersSelectComponent;
	// 	}

	// 	return component;
	// }

	private getComponentType(filter: Filter): Type<any> {
		let templateType = filter.templateType;

		switch (this.filterTemplateType) {
			case FilterTemplateType.BUTTON_SELECTION:
				templateType = FilterTemplateType.BUTTON_SELECTION;

				break;
			case FilterTemplateType.SINGLE_DROPDOWN_SELECTION:
				templateType = FilterTemplateType.SINGLE_DROPDOWN_SELECTION;

				break;
			default:
		}

		return this.templateMapper[templateType];
	}

	// private setInput<T>(componentRef: ComponentRef<T>): void {
	// 	// Set `Input` decorators
	// 	componentRef.setInput('control', this.filter);
	// 	componentRef.setInput('form', this.form);
	// }

	/**
	 * Set the Output events emitter of the component
	 *
	 * @param componentRef
	 */
	private setOutput<T>(componentRef: ComponentRef<T>): void {
		const NG_UNSUBSCRIBE = new Subject<void>();

		// Listen to `changed` event
		componentRef.instance['changed'].pipe(takeUntil(NG_UNSUBSCRIBE)).subscribe(() => this.changed.emit(this.filter));

		// Destroy the subscription
		componentRef.onDestroy(() => {
			NG_UNSUBSCRIBE.next();
			NG_UNSUBSCRIBE.complete();
		});
	}

	ngAfterViewInit(): void {
		this.createControlContainer(this.filter);
	}

	ngOnChanges(changes: SimpleChanges): void {
		CommonUtil.handleNgOnChanges(changes['filter'], (filter: Filter) => {
			if (this.componentRef) {
				this.componentRef.setInput('filter', { ...filter });
			}
		});
	}
}
