import { Component, Input } from '@angular/core';
import { PageMetaData } from '@core/models';

@Component({
	standalone: true,
	selector: 'findqo-ad-list-pagination-info',
	template: `
		<div class="text-body-regular fg-general-5">
			Showing
			{{ (+metadata?.currentPage - 1) * +metadata?.perPage + 1 }}
			-
			{{ +metadata.perPage * +metadata?.currentPage > +metadata?.total ? metadata?.total : +metadata.perPage * +metadata?.currentPage }}
			of
			<span class="fw-600">{{ metadata?.total }}</span>
		</div>
	`,
})
export class AdListPaginationInfoComponent {
	@Input() metadata!: PageMetaData;
}
