import { Component, Inject } from '@angular/core';
import { AbstractFiltersFormComponent } from '../../abstracts/filters-form.abstract';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { FilterTemplateType } from '@core/enums';

@Component({
    selector: 'findqo-desktop-filter-dialog',
    templateUrl: './desktop-filter-dialog.component.html',
    styleUrl: './desktop-filter-dialog.component.scss',
    standalone: false
})
export class DesktopFilterDialogComponent extends AbstractFiltersFormComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public dialogData,
		private readonly dialogRef: MatDialogRef<DesktopFilterDialogComponent>
	) {
		super();
	}

	readonly filterTemplateType = FilterTemplateType;

	clearAllFilters(): void {
		this.filtersService.clearAllFilterControl();
		this.dialogRef.close();
	}

	onMinMaxChange(): void {
		this.filtersService.updateRoute();
	}
}
