import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID } from '@angular/core';
import { Params } from '@angular/router';
import { Breakpoint, FilterType } from '@core/enums';
import { Ad, Aisle, PageMetaData } from '@core/models';
import { BreakpointObserverService } from '@core/services/breakpoint-observer.service';
import { RouteService } from '@core/services/route.service';
import { environment } from '@environments/environment';
import { FiltersFacade } from '@feature/general/filters/filters.facade';
import { FiltersService } from '@feature/general/filters/services/filters.service';
import { ButtonType } from '@shared/components/button/button.component';
import { DestroyService } from '@shared/services/destroy.service';
import { filter, map, pairwise, startWith, takeUntil, tap } from 'rxjs';

@Component({
    selector: 'findqo-filters',
    templateUrl: './filters.component.html',
    styleUrls: ['./filters.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class FiltersComponent implements OnInit, OnDestroy {
	constructor(
		private readonly filtersFacade: FiltersFacade,
		public readonly bpoService: BreakpointObserverService,
		public readonly filterService: FiltersService,
		private readonly routeService: RouteService,
		private readonly destroyService: DestroyService,
		@Inject(PLATFORM_ID) private platformId: string
	) {}

	@Input() adList: Ad[] = [];
	@Input() metadata: PageMetaData;
	@Input() isEmptyList = false;

	selectedAisle: Aisle;
	isBrowser = isPlatformBrowser(this.platformId);

	readonly filtersVm$ = this.filtersFacade.getFiltersViewModel$(this.routeService.routeData$);
	readonly breakpoint = Breakpoint;
	readonly buttonType = ButtonType;
	readonly currentVersionFeature = environment.featureVersion;

	readonly skeletonTheme = { border: '4', width: '100%', margin: '0px', height: '40px' };

	private onParamsChange(): void {
		/**
		 * Child component can't get parents params.
		 * By using RouteService routeData$, we can able to get the params from parent.
		 */
		this.routeService.routeData$
			.pipe(
				takeUntil(this.destroyService),
				startWith(undefined),
				pairwise(),
				tap(([previousParams, newParams]: Params[]) => {
					// Reset the Filters service on change of section or Aisle
					if (
						previousParams?.[FilterType.SECTION] !== newParams[FilterType.SECTION] ||
						previousParams[FilterType.AISLE] !== newParams[FilterType.AISLE] ||
						previousParams[FilterType.STAND] !== newParams[FilterType.STAND]
					) {
						this.filterService.resetService();
					}
				}),
				// eslint-disable-next-line @typescript-eslint/no-unused-vars
				map(([_previousParams, newParams]: Params[]) => {
					let location = newParams[FilterType.LOCATION_REFERENCE];

					if (newParams['pageData'][FilterType.LOCATION_REFERENCE] !== location) {
						location = newParams[FilterType.LOCATION_REFERENCE] + ',' + newParams['pageData'][FilterType.LOCATION_REFERENCE];
					}

					return {
						[FilterType.SECTION]: newParams[FilterType.SECTION],
						[FilterType.LOCATION_REFERENCE]: location,
						[FilterType.AISLE]: newParams[FilterType.AISLE],
						[FilterType.STAND]: newParams[FilterType.STAND],
					};
				}),
				filter((params: Params) => {
					// Check if params are changed
					// Only call load Filters API if params have changed or Forms is empty

					// Exclude location param from check
					const PARAMS = { ...params };
					const PREVIOUS_PARAMS = { ...this.filtersFacade.getParams() };

					delete PARAMS['location'];
					delete PREVIOUS_PARAMS['location'];

					return JSON.stringify(PARAMS) !== JSON.stringify(PREVIOUS_PARAMS) || !this.filterService.getFiltersForm();
				})
			)
			.subscribe((params: Params) => {
				this.filtersFacade.setParams(params);
				this.filtersFacade.loadFilterList(params);
			});
	}

	ngOnInit(): void {
		if (this.isBrowser) {
			this.onParamsChange();
		}
	}

	ngOnDestroy(): void {
		this.filterService.resetService();
		this.filtersFacade.resetFacade();
	}
}
