import { Component, Input, OnInit } from '@angular/core';
import { AbstractControl } from '@angular/forms';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { FilterAction, FilterType, FiltersPanelClass } from '@core/enums';
import { Filter } from '@core/models';
import { NgbOffcanvas } from '@ng-bootstrap/ng-bootstrap';
import { ButtonType } from '@shared/components/button/button.component';
import { ToggleResetFiltersPipe } from '@shared/pipes/filters/filters.pipe';
import { Observable, skip } from 'rxjs';
import { FiltersFacade } from '../../filters.facade';
import { FiltersService } from '../../services/filters.service';
import { BaseFiltersComponent } from '../base-filters/base-filters.component';

@Component({
    selector: 'findqo-filters-menu',
    templateUrl: './filters-menu.component.html',
    styleUrls: ['./filters-menu.component.scss'],
    providers: [ToggleResetFiltersPipe],
    standalone: false
})
export class FiltersMenuComponent implements OnInit {
	constructor(
		private readonly ngbOffCanvas: NgbOffcanvas,
		private readonly matBottomSheet: MatBottomSheet,
		private readonly filtersFacade: FiltersFacade,
		private readonly filtersService: FiltersService,
		private readonly toggleResetFiltersPipe: ToggleResetFiltersPipe
	) {
		this.toggleResetFilters$ = this.toggleResetFiltersPipe.transform();
		this.toggleProgressBar$ = this.filtersFacade.getToggleProggresBar$();
		this.adsCount$ = this.filtersFacade.getAdsCount$();
	}

	@Input() filterList: Filter[] = [];

	toggleProgressBar$: Observable<boolean>;
	toggleResetFilters$: Observable<boolean>;
	adsCount$: Observable<number>;
	btnSubmitLabel = 'Show Results';
	isAdListCountLoading = false;

	readonly buttonType = ButtonType;
	readonly filterType = FilterType;

	// Easy getter for the form controls
	get fc(): { [p: string]: AbstractControl } {
		return this.filtersService.getFiltersForm().controls;
	}

	onClose(): void {
		this.ngbOffCanvas.dismiss(FilterAction.RESTORE);
	}

	onOpenBaseFilterBottomSheet(filter: Filter): void {
		this.isAdListCountLoading = true;

		const currentControlValue = this.fc[filter.paramName]?.value;
		const bottomSheetRef = this.matBottomSheet.open(BaseFiltersComponent, {
			data: { ...filter, isOpenFromFiltersMenu: true },
			panelClass: FiltersPanelClass.FILTERS_MENU.split(/\s+/),
		});

		bottomSheetRef.instance['submitted'].subscribe(() => this.filtersFacade.setToggleProgressBar(true));

		bottomSheetRef.afterDismissed().subscribe((action: FilterAction) => {
			this.isAdListCountLoading = false;

			switch (action) {
				case FilterAction.UPDATE:
					if (filter.paramName === FilterType.STAND) {
						this.filtersFacade.loadFilterList({ ...this.filtersFacade.getParams(), stand: this.fc[FilterType.STAND].value });
					}

					this.filtersFacade.loadAdsCount({
						...this.filtersService.createGetAdListRequestParams(),
					});

					break;
				case FilterAction.RESTORE:
					this.filtersService.restorePreviousFilterValue(filter, currentControlValue);

					break;
				default:
			}
		});
	}

	onResetFilters(): void {
		this.filtersService.clearAllFilterControl();
		this.ngbOffCanvas.dismiss(FilterAction.UPDATE);
	}

	onSubmit(): void {
		this.ngbOffCanvas.dismiss(FilterAction.UPDATE);
	}

	trackByFilterParamName(_index: number, filter: Filter): string {
		return filter.paramName;
	}

	private initDeclarations(): void {
		this.adsCount$.pipe(skip(1)).subscribe((count: number) => (this.btnSubmitLabel = `Show ${count} Results`));
	}

	ngOnInit(): void {
		this.initDeclarations();
	}
}
