import { Component, EventEmitter, Input, OnInit, Output, inject } from '@angular/core';
import { AbstractControl, FormControl } from '@angular/forms';
import { ActivatedRoute, ParamMap, Params, Router } from '@angular/router';
import { FilterTemplateType, FilterType, SectionType } from '@core/enums';
import { CommonFacade } from '@core/facades/common.facade';
import { Filter } from '@core/models';
import { BreakpointObserverService } from '@core/services/breakpoint-observer.service';
import { ButtonType } from '@shared/components/button/button.component';
import { FiltersFacade } from '../filters.facade';
import { FiltersService } from '../services/filters.service';

@Component({
	template: '',
	standalone: false,
})
export abstract class AbstractControlBaseComponent implements OnInit {
	public readonly route = inject(ActivatedRoute);
	public readonly router = inject(Router);
	public readonly bpoService = inject(BreakpointObserverService);
	public readonly filtersService = inject(FiltersService);
	public readonly filtersFacade = inject(FiltersFacade);
	public readonly commonFacade = inject(CommonFacade);

	@Input() filter: Filter;
	@Input() isOpenFromFilterMenu = false;
	@Output() changed = new EventEmitter<Filter>();

	selectedOption: string;
	selectedOptionList: string[];
	allBtnOptionLabel: string;
	section: SectionType;

	// Search
	searchControl = new FormControl();
	searchKeys = ['name'];
	toggleSearch = false;

	readonly buttonType = ButtonType;
	readonly filterTemplateType = FilterTemplateType;
	readonly filterType = FilterType;

	// Easy getter for the form controls
	get fc(): { [p: string]: AbstractControl } {
		return this.filtersService.getFiltersForm()?.controls;
	}

	get othersFc(): { [p: string]: AbstractControl } {
		return this.filtersService.getOthersForm()?.controls;
	}

	trackByFilterValueId(_index: number, value: { id: string }): string {
		return value.id;
	}

	/**
	 * Repopulate the Filter child reference if neeeded
	 *
	 * e.g.
	 * Stand and Shelf
	 */
	getChildFilterValue(): void {
		if (this.filter?.childReferenceParamName) {
			if (this.filter.paramName === FilterType.STAND) {
				// If stand, no need to call an API to the dropdown value of Shelve
				this.filtersFacade.setFilterShelveList(this.filter, this.fc[FilterType.STAND].value);
			} else {
				const CHILD_FILTER = this.filtersFacade.getFilter(this.filter?.childReferenceParamName);

				this.loadFilterDropdownValueList(CHILD_FILTER);
			}
		}
	}

	private async loadFilterDropdownValueList(filter: Filter): Promise<void> {
		try {
			await this.filtersFacade.loadFilterDropdownValueList(filter, this.filtersService.getFiltersForm());
		} catch {
			// Add error handling here...
		}
	}

	private init(): void {
		// Get all selected filters from query params
		this.route.queryParamMap.subscribe((queryParamMap: ParamMap) => (this.selectedOptionList = queryParamMap.getAll(this.filter.paramName)));

		// Get current active section
		this.route.params.subscribe((param) => (this.section = param?.['section']));

		// Get the value of current selected filter from query params
		this.route.queryParams.subscribe((queryParams: Params) => (this.selectedOption = queryParams[this.filter.paramName]));

		// Toggle filter values/dropdown options search
		this.toggleSearch =
			(!this.filter.isMinMax &&
				this.filter.templateType !== FilterTemplateType.BOOLEAN_SELECTION &&
				(this.filter.values?.all?.length >= 10 || this.filter.values?.length >= 10)) ||
			this.filter.paramName === FilterType.COUNTY_AREA;

		// Set All option label
		const TRANSLATIONS = this.commonFacade.getTranslations();
		const LABEL =
			this.filter.paramName === FilterType.COUNTY
				? // TODO: Ask for the translation for `in` or `All in` or `All in Ireland`.
				  `All in Ireland`
				: `All`;

		if (this.filter.paramName === FilterType.IS_OWNER_OCCUPIED || this.filter.paramName === FilterType.IS_ONE_PLUS_PERSON_ALLOWED) {
			this.allBtnOptionLabel = `Don't Mind`;
		} else {
			this.allBtnOptionLabel = `${LABEL}`;
		}
	}

	ngOnInit(): void {
		this.init();
	}
}
