import { Component, inject, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { AisleType, CountyType, FilterAction, FiltersPanelClass, FilterType } from '@core/enums';
import { Aisle, Filter, RecentSearch } from '@core/models';
import { NgbOffcanvas, NgbOffcanvasRef } from '@ng-bootstrap/ng-bootstrap';
import { AbstractFiltersFormComponent } from '../../abstracts/filters-form.abstract';
import { BaseFiltersComponent } from '../../components/base-filters/base-filters.component';
import { isPlatformBrowser } from '@angular/common';
import { Observable } from 'rxjs';
import { IntercomService } from '@core/services/intercom.service';
import { APP_ROUTE } from '@core/constants';
import { RouteData } from '@core/services/route.service';
import { FiltersFacade } from '../../filters.facade';

@Component({
    selector: 'findqo-filters-mobile',
    templateUrl: './filters-mobile.component.html',
    styleUrls: ['./filters-mobile.component.scss'],
    standalone: false
})
export class FiltersMobileComponent extends AbstractFiltersFormComponent implements OnInit, OnDestroy {
	constructor(
		private readonly ngbOffCanvas: NgbOffcanvas,
		private readonly matBottomSheet: MatBottomSheet,
		@Inject(PLATFORM_ID) public platformId,
		private filterFacade: FiltersFacade
	) {
		super();
		this.recentSearches$ = this.commonFacade.recentSearches$;
	}

	@Input() routeData!: RouteData;

	@ViewChild('offcanvasMoreFilters') offcanvasMoreFiltersRef: TemplateRef<NgbOffcanvasRef>;
	@ViewChild('offcanvasBaseFilters') offcanvasBaseFiltersRef: TemplateRef<NgbOffcanvasRef>;
	recentSearches$: Observable<RecentSearch[]>;

	public readonly offcanvas = inject(NgbOffcanvas);
	private intercomService: IntercomService;
	isRent = true;
	isBuy = false;
	btnSubmitLabel = 'Done';
	Labelname = 'Rent';

	onOpenFiltersMenuOffCanvas(content: TemplateRef<NgbOffcanvasRef>): void {
		const currentFormValue = { ...this.form?.value };
		const offCanvasRef = this.ngbOffCanvas.open(content, { position: 'bottom', panelClass: FiltersPanelClass.FILTERS_MENU });

		offCanvasRef.dismissed.subscribe((action: FilterAction) => {
			switch (action) {
				case FilterAction.RESTORE:
					this.filtersService.restoreAllPreviousFilterValue(currentFormValue);
					break;
				case FilterAction.UPDATE:
					this.filtersService.updateRoute();
			}
		});
	}

	onOpenBaseFilterBottomSheet(filter: Filter): void {
		const PANEL_CLASS = filter.isMinMax ? FiltersPanelClass.BASE_MIN_MAX_FILTER : FiltersPanelClass.BASE_SELECTION_FILTER;
		const bottomSheetRef = this.matBottomSheet.open(BaseFiltersComponent, {
			data: { ...filter, isOpenFromFiltersMenu: false },
			panelClass: PANEL_CLASS.split(/\s+/),
		});
		const currentControlValue = this.fc[filter.paramName]?.value;

		bottomSheetRef.backdropClick().subscribe(() => this.filtersService.restorePreviousFilterValue(filter, currentControlValue));

		bottomSheetRef.afterDismissed().subscribe((action: FilterAction) => {
			switch (action) {
				case FilterAction.UPDATE:
					this.filtersService.updateRoute();

					break;
				case FilterAction.RESTORE:
					this.filtersService.restorePreviousFilterValue(filter, currentControlValue);

					break;
				default:
					this.filtersService.restorePreviousFilterValue(filter, currentControlValue);
			}

			this.filtersFacade.setToggleProgressBar(false);
		});
	}

	onSelectAisle(aisle: Aisle): void {
		const ROUTE_DATA = this.routeService.getRouteData();
		const params = this.filterFacade.getParams();
		const queryParams = this.route.snapshot?.queryParams;
		const stand = ['houses', undefined, 'apartments'].includes(params['stand']) ? params['stand'] : null;

		// Prevent the navigation back to `section/aisle` when aisle had stand `section/aisle/stand`
		if (ROUTE_DATA?.stand && ROUTE_DATA?.aisle === aisle.reference) {
			return;
		}
		if (
			(aisle.reference.includes('share-residential') && params?.['aisle']?.includes('rent-residential')) ||
			(aisle.reference.includes('rent-residential') && params?.['aisle']?.includes('share-residential'))
		) {
			this.router.navigate([ROUTE_DATA['section'], ROUTE_DATA['location'], aisle.reference, stand ? stand : []], {
				queryParams,
				queryParamsHandling: 'merge',
			});
			return;
		}
		this.router.navigate([ROUTE_DATA['section'], CountyType.DEFAULT, aisle.reference]);
	}

	fetchRecentSearch(): void {
		this.commonFacade.fetchRecentSearch();
	}

	ngOnDestroy(): void {
		if (isPlatformBrowser(this.platformId)) {
			this.fetchRecentSearch();
		}

		this.ngbOffCanvas.dismiss();
	}
	onOpenFiltersforRent(content: TemplateRef<NgbOffcanvasRef>) {
		const currentFormValue = { ...this.form?.value };
		const offCanvasRef = this.ngbOffCanvas.open(content, { position: 'bottom', panelClass: FiltersPanelClass.BASE_MIN_MAX_FILTER });

		offCanvasRef.dismissed.subscribe((action: FilterAction) => {
			switch (action) {
				case FilterAction.RESTORE:
					this.filtersService.restoreAllPreviousFilterValue(currentFormValue);
					break;
				case FilterAction.UPDATE:
					this.filtersService.updateRoute();
			}
		});
	}
	onClose(): void {
		this.offcanvas.dismiss();

		if (this.router.url === '/') {
			this.intercomService.toggelIntercomPopup(false);
		}
	}
	onPropertyclick(value: string) {
		if (value == 'Rent') {
			this.isRent = true;
			this.isBuy = false;
			this.Labelname = 'Rent';
		} else {
			this.isRent = false;
			this.isBuy = true;
			this.Labelname = 'Buy';
		}
	}
	onResetFilters(): void {
		this.isRent = true;
		this.isBuy = false;
		this.Labelname = 'Rent';
	}

	onSubmit(value: string): void {
		this.ngbOffCanvas.dismiss(FilterAction.UPDATE);
		if (value == 'Rent') {
			this.router.navigate([APP_ROUTE.rentAdsList]);
		} else {
			this.router.navigate([APP_ROUTE.sellresidential]);
		}
	}
}
