import { Component, OnInit, ViewChild } from '@angular/core';
import { MatListOption, MatSelectionList } from '@angular/material/list';
import { FilterTemplateType, FilterType } from '@core/enums';
import { Filter, FilterDropdownValue } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';
import { AbstractControlBaseComponent } from '@feature/general/filters/abstracts/control-base.abstract';
import { FILTERS_KEY_REFERENCED } from '@feature/general/filters/constants/filters.constants';
import { threadId } from 'worker_threads';

const INITIAL_OPTION_COUNT = 5;

@Component({
    selector: 'findqo-filters-button-selection',
    templateUrl: './filters-button-selection.component.html',
    styleUrls: ['./filters-button-selection.component.scss'],
    standalone: false
})
export class FiltersButtonSelectionComponent extends AbstractControlBaseComponent implements OnInit {
	@ViewChild('matSelectionList') matSelectionListRef: MatSelectionList;

	toggleViewMore = false;
	optionCount = INITIAL_OPTION_COUNT;
	resetBtnLabel = 'Any';

	onToggleViewMore(filter: Filter): void {
		this.toggleViewMore = !this.toggleViewMore;
		this.optionCount = this.toggleViewMore ? filter.values.length : INITIAL_OPTION_COUNT;
	}

	onSelectFilterValue(filterValue: FilterDropdownValue, matListOption: MatListOption): void {
		switch (this.filter.templateType) {
			case FilterTemplateType.MULTI_SELECTION:
				this.filtersService.updateControlArrayValue(this.filter, filterValue);
				this.filtersService.updateRoute();

				break;
			case FilterTemplateType.SINGLE_SELECTION:
			case FilterTemplateType.BOOLEAN_SELECTION:
				let formValue = FILTERS_KEY_REFERENCED.includes(this.filter.paramName) ? filterValue.reference : filterValue.id;

				// Deselect all if the selected value is already pinned as selected.
				if (matListOption.selected) {
					formValue = null;
					CommonUtil.handleExpressionChangedError(() => this.matSelectionListRef.deselectAll());
				}

				this.filtersService.updateControlValue(this.filter, formValue);
				this.filtersService.updateRoute();
				this.getChildFilterValue();

				break;
			default:
		}
	}

	onReset(): void {
		if (!this.fc[this.filter.paramName]?.value || !this.fc[this.filter.paramName]?.value?.length) {
			return;
		}

		switch (this.filter.templateType) {
			case FilterTemplateType.MULTI_SELECTION:
				this.filtersService.resetFormArray(this.filter.paramName);
				this.filtersService.updateRoute();

				break;
			case FilterTemplateType.SINGLE_SELECTION:
			case FilterTemplateType.BOOLEAN_SELECTION:
				this.filtersService.updateControlValue(this.filter, '');
				this.filtersService.updateRoute();

				break;
			default:
		}
	}

	private initDeclsaration(): void {
		const SELECTED_FILTER_VALUE = this.fc[this.filter.paramName]?.value;
		const FILTER_VALUES = this.filter?.values;

		if (SELECTED_FILTER_VALUE && FILTER_VALUES) {
			if (Array.isArray(SELECTED_FILTER_VALUE)) {
				this.toggleViewMore = !!SELECTED_FILTER_VALUE.some((value) => FILTER_VALUES.findIndex((filterValue) => +filterValue.id === +value.id) >= 5);
			} else {
				this.toggleViewMore =
					FILTER_VALUES.findIndex((filterValue) => filterValue.id === SELECTED_FILTER_VALUE || filterValue.reference === SELECTED_FILTER_VALUE) >= 5;
			}

			this.optionCount = this.toggleViewMore ? this.filter.values.length : INITIAL_OPTION_COUNT;
		}

		this.resetBtnLabel =
			this.filter.paramName === FilterType.IS_OWNER_OCCUPIED || this.filter.paramName === FilterType.IS_ONE_PLUS_PERSON_ALLOWED ? `Don't Mind` : 'Any';
	}

	override ngOnInit(): void {
		super.ngOnInit();
		this.initDeclsaration();
	}
}
