import { AfterViewInit, Component, ContentChild, OnChanges, OnInit, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { FilterTemplateType, FilterType, SectionType } from '@core/enums';
import { Filter, FilterDropdownValue } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';
import { AbstractControlBaseComponent } from '@feature/general/filters/abstracts/control-base.abstract';
import { FiltersUtil } from '@feature/general/filters/utils/filters.utils';
import { DestroyService } from '@shared/services/destroy.service';
import { takeUntil } from 'rxjs';

const INPUT_VALUES = {
	max: {
		placeholder: 'Max',
		formControlName: 'max',
	},
	min: {
		placeholder: 'Min',
		formControlName: 'min',
	},
};

@Component({
    selector: 'findqo-filters-min-max',
    templateUrl: './filters-min-max.component.html',
    styleUrls: ['./filters-min-max.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class FiltersMinMaxComponent extends AbstractControlBaseComponent implements OnInit, AfterViewInit, OnChanges {
	constructor(private readonly fb: FormBuilder, private readonly destroyService: DestroyService) {
		super();
	}

	@ViewChild('dropdownTemplate') dropdownRef: TemplateRef<any>;
	@ViewChild('inputTemplate') inputRef: TemplateRef<any>;
	@ContentChild('minmaxTemplate') minMaxRef: TemplateRef<any>;

	minMaxForm: FormGroup;
	filterValueList: FilterDropdownValue[] = [];

	readonly inputValues = INPUT_VALUES;

	onMinMaxDropdownChanged(): void {
		const MIN = FiltersUtil.getFilterDropdownValue(this.filterValueList, 'id', this.minMaxForm.get('min').value);
		const MAX = FiltersUtil.getFilterDropdownValue(this.filterValueList, 'id', this.minMaxForm.get('max').value);

		this.filtersService.updateMinMaxValue(this.filter, +MIN.name.replaceAll(',', ''), +MAX.name.replaceAll(',', ''));

		// TODO: Need to confirm if this is needed also on quick filters
		this.loadAdsCount();

		this.changed.emit(this.filter);
	}

	onMinMaxInputChanged(): void {
		this.filtersService.updateMinMaxValue(this.filter, +this.minMaxForm.get('min').value, +this.minMaxForm.get('max').value);

		// TODO: Need to confirm if this is needed also on quick filters
		this.loadAdsCount();

		this.changed.emit(this.filter);
	}

	private loadAdsCount(): void {
		if (this.isOpenFromFilterMenu) {
			this.filtersFacade.loadAdsCount({
				...this.filtersService.getFiltersForm().value,
			});
		}
	}

	private createMinMaxDropdownForm(): void {
		let initialMinValue = this.filterValueList[0]?.id;
		let initialMaxValue = this.filterValueList[this.filterValueList.length - 1]?.id;

		if (this.fc[this.filter.paramName]?.value) {
			const MIN_MAX_FILTERS_FORM_VALUE = this.fc[this.filter.paramName].value.split(',');

			initialMinValue = FiltersUtil.getFilterDropdownValue(this.filterValueList, 'name', CommonUtil.numberWithCommas(MIN_MAX_FILTERS_FORM_VALUE[0]))?.id;
			initialMaxValue = FiltersUtil.getFilterDropdownValue(this.filterValueList, 'name', CommonUtil.numberWithCommas(MIN_MAX_FILTERS_FORM_VALUE[1]))?.id;
		}

		this.minMaxForm = this.fb.group({
			min: [initialMinValue],
			max: [initialMaxValue],
		});
	}

	private createMinMaxInputForm(): void {
		let initialMinValue = '';
		let initialMaxValue = '';

		if (this.fc[this.filter.paramName]?.value) {
			const MIN_MAX_FILTERS_FORM_VALUE = this.fc[this.filter.paramName].value.split(',');

			initialMinValue = MIN_MAX_FILTERS_FORM_VALUE[0];
			initialMaxValue = MIN_MAX_FILTERS_FORM_VALUE[1];
		}

		this.minMaxForm = this.fb.group({
			min: [initialMinValue],
			max: [initialMaxValue],
		});
	}

	private initDeclarations(): void {
		if (this.filter.templateType === FilterTemplateType.MIN_MAX_DROPDOWN) {
			this.filterValueList = [...(this.filter?.values || [])].splice(this.filter.paramName !== FilterType.OTHER_TENANT_SHARE_COUNT ? 1 : 0);
			this.createMinMaxDropdownForm();
		} else {
			this.createMinMaxInputForm();
		}
	}

	private setMinMaxRef(): void {
		/**
		 * HACK: Fix expression has changed after it was checked
		 * We are using setTimeout() to defer this code to another Javascript Virtual Machine turn, without specifying the value for timeout.
		 */
		setTimeout(() => {
			this.minMaxRef = this.filter.templateType === FilterTemplateType.MIN_MAX_DROPDOWN ? this.dropdownRef : this.inputRef;
		});
	}

	private onResetFiltersForm(): void {
		this.fc[this.filter.paramName]?.valueChanges.pipe(takeUntil(this.destroyService)).subscribe((value: string) => {
			if (!value) {
				if (this.filter.templateType === FilterTemplateType.MIN_MAX_DROPDOWN) {
					this.minMaxForm.get('min').patchValue(this.filterValueList[0]?.id);
					this.minMaxForm.get('max').patchValue(this.filterValueList[this.filterValueList.length - 1]?.id);
				} else {
					this.minMaxForm.get('min').patchValue('');
					this.minMaxForm.get('max').patchValue('');
				}
			}
		});
	}

	override ngOnInit(): void {
		super.ngOnInit();

		this.initDeclarations();
		this.onResetFiltersForm();
	}

	ngAfterViewInit(): void {
		this.setMinMaxRef();
	}

	ngOnChanges(changes: SimpleChanges): void {
		CommonUtil.handleNgOnChanges(changes['filter'], (filter: Filter) => {
			if (filter.values) {
				this.initDeclarations();
				this.onResetFiltersForm();
			}
		});
	}
}
