import { Component, OnInit, ViewChild } from '@angular/core';
import { FilterType } from '@core/enums';
import { CommonPlaceAdService } from '@core/services/common-place-ad.service';
import { IntercomService } from '@core/services/intercom.service';
import { ViewportService } from '@core/services/viewport.service';
import { FiltersFacade } from '@feature/general/filters/filters.facade';
import { FiltersService } from '@feature/general/filters/services/filters.service';
import { ButtonType } from '@shared/components/button/button.component';
import { CdkDropdownComponent } from '@shared/components/cdk-dropdown/cdk-dropdown.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { DestroyService } from '@shared/services/destroy.service';

@Component({
    selector: 'findqo-ad-list-layout',
    templateUrl: './ad-list-layout.component.html',
    styleUrls: ['./ad-list-layout.component.scss'],
    providers: [DestroyService],
    standalone: false
})
export class AdListLayoutComponent implements OnInit {
	constructor(
		private filtersFacade: FiltersFacade,
		private sharedAdsFacade: SharedAdsFacade,
		private filtersService: FiltersService,
		private intercomService: IntercomService,
		private viewport: ViewportService,
		private readonly commonPlaceAdService: CommonPlaceAdService
	) {}

	@ViewChild(CdkDropdownComponent) cdkDropdown: CdkDropdownComponent;

	sortList$ = this.filtersFacade.getSortList$();
	adsVm$ = this.sharedAdsFacade.getAdsVm$();
	toggleSort = false;

	readonly filterType = FilterType;
	readonly buttonType = ButtonType;

	onOpenSort(): void {
		this.toggleSort = true;
		this.cdkDropdown.show();
	}

	onCloseSort(): void {
		this.toggleSort = false;
		this.cdkDropdown.hide();
	}

	onPlaceAd() {
		this.commonPlaceAdService.gotoPlaceAd();
	}

	onReset(): void {
		this.filtersService.clearAllFilterControl();
	}

	ngOnInit(): void {
		if (this.viewport.mobile) {
			this.intercomService.toggelIntercomPopup(true);
		}
	}
}
