import { Component } from '@angular/core';
import { FilterType } from '@core/enums';
import { FilterLimitToPipe } from '@shared/pipes';
import { AbstractControlBaseComponent } from '../../abstracts/control-base.abstract';

@Component({
    selector: 'findqo-filters-dropdown-selection',
    templateUrl: './filters-dropdown-selection.component.html',
    styleUrls: ['./filters-dropdown-selection.component.scss'],
    providers: [FilterLimitToPipe],
    standalone: false
})
export class FiltersDropdownSelectionComponent extends AbstractControlBaseComponent {
	onSelectionChange(reference: string): void {
		this.onFilterChange();
	}

	/**
	 * Handle filter change
	 *
	 * • On bottom sheet, only on close event we are triggering the update or submit filters
	 * • On side Filters form, trigger immediately the update or submit filters on change
	 */
	private onFilterChange(): void {
		this.filtersService.updateRoute();

		// If parent Filter has a child reference Filter, get the child reference Filter value
		if (this.fc[this.filter.paramName]?.value) {
			this.getChildFilterValue();
		}
	}

	resetSearchControl(): void {
		this.searchControl.patchValue('');
	}
}
