import { AfterViewInit, Component, ContentChild, EventEmitter, Inject, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MAT_BOTTOM_SHEET_DATA, MatBottomSheetRef } from '@angular/material/bottom-sheet';
import { FilterAction, FilterTemplateType, FilterType } from '@core/enums';
import { Filter } from '@core/models/filters.model';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable } from 'rxjs';
import { FiltersFacade } from '../../filters.facade';
import { FiltersService } from '../../services/filters.service';
import { CommonUtil } from '@core/utils/common.util';

interface BottomSheetData extends Filter {
	isOpenFromFiltersMenu: boolean;
}

@Component({
    selector: 'findqo-base-filters',
    templateUrl: './base-filters.component.html',
    styleUrls: ['./base-filters.component.scss'],
    standalone: false
})
export class BaseFiltersComponent implements OnInit, AfterViewInit {
	constructor(
		@Inject(MAT_BOTTOM_SHEET_DATA) public readonly bottomSheetData: BottomSheetData,
		private readonly bottomSheetRef: MatBottomSheetRef<BaseFiltersComponent>,
		private readonly filtersFacade: FiltersFacade,
		private readonly filtersService: FiltersService
	) {
		this.isRangeSearchEnabled$ = this.filtersFacade.getIsRangeSearchEnabled$();
	}

	@Output() submitted = new EventEmitter();

	@ViewChild('selectionTemplate') selectionRef: TemplateRef<Element>;
	@ViewChild('minMaxTemplate') minMaxRef: TemplateRef<Element>;
	@ContentChild('filterControlTemplate') filterControlRef: TemplateRef<Element>;

	isRangeSearchEnabled$: Observable<boolean>;
	form: FormGroup;
	filter: Filter;
	btnSubmitLabel = 'Show Results';
	isLoading = false;
	toggleActions = false;

	readonly buttonType = ButtonType;
	readonly filterType = FilterType;
	readonly filterTemplateType = FilterTemplateType;

	onClose(): void {
		this.bottomSheetRef.dismiss(FilterAction.RESTORE);
	}

	onSubmitFilters(): void {
		this.submitted.emit();
		this.bottomSheetRef.dismiss(FilterAction.UPDATE);
	}

	onClearFilter(filter: Filter): void {
		this.filtersService.clearFilterControl(filter);
	}

	private initDeclarations(): void {
		this.filter = this.bottomSheetData;
		this.form = this.filtersService.getFiltersForm();
		this.toggleActions = this.filter.templateType !== FilterTemplateType.SINGLE_SELECTION && this.filter.templateType !== FilterTemplateType.BOOLEAN_SELECTION;
		this.btnSubmitLabel = this.bottomSheetData?.isOpenFromFiltersMenu ? 'Done' : 'Show Results';
	}

	private setFilterControlRef(): void {
		/**
		 * HACK: Fix expression has changed after it was checked
		 * We are using setTimeout() to defer this code to another Javascript Virtual Machine turn, without specifying the value for timeout.
		 */
		CommonUtil.handleExpressionChangedError(() => (this.filterControlRef = this.filter.isMinMax ? this.minMaxRef : this.selectionRef));
	}

	ngOnInit(): void {
		this.initDeclarations();
	}

	ngAfterViewInit(): void {
		this.setFilterControlRef();
	}
}
