import { Component, Input, ViewChild } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { FiltersPanelClass } from '@core/enums';
import { PageMetaData, Seo, SortBy } from '@core/models';
import { CdkDropdownComponent } from '@shared/components/cdk-dropdown/cdk-dropdown.component';
import { FiltersSortComponent } from '../../components/filters-sort/filters-sort.component';
import { FiltersFacade } from '../../filters.facade';

@Component({
    selector: 'findqo-filters-mobile-header',
    templateUrl: './filters-mobile-header.component.html',
    styleUrl: './filters-mobile-header.component.scss',
    standalone: false
})
export class FiltersMobileHeaderComponent {
	constructor(
		private readonly matBottomSheet: MatBottomSheet,
		private filtersFacade: FiltersFacade
	) {}
	@ViewChild(CdkDropdownComponent) cdkDropdown: CdkDropdownComponent;
	@Input() adListCount = 0;
	@Input() sortList: SortBy[] = [];
	@Input() isEmptyList: boolean;
	@Input() seo: Seo;
	@Input() metadata: PageMetaData;

	sortList$ = this.filtersFacade.getSortList$();
	toggleSort = false;

	onOpenSortBottomSheet(): void {
		this.matBottomSheet.open(FiltersSortComponent, {
			panelClass: FiltersPanelClass.BASE_MIN_MAX_FILTER.split(/\s+/),
		});
	}
	onOpenSort(): void {
		this.toggleSort = true;
		this.cdkDropdown.show();
	}
	onCloseSort(): void {
		this.toggleSort = false;
		this.cdkDropdown.hide();
	}
}
