import { AfterViewInit, Component, ContentChild, Optional, TemplateRef, ViewChild } from '@angular/core';
import { MatBottomSheetRef } from '@angular/material/bottom-sheet';
import { FilterAction, FilterTemplateType, FilterType } from '@core/enums';
import { FilterDropdownValue } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';
import { AbstractControlBaseComponent } from '@feature/general/filters/abstracts/control-base.abstract';
import { BaseFiltersComponent } from '@feature/general/filters/components/base-filters/base-filters.component';
import { FILTERS_KEY_REFERENCED } from '@feature/general/filters/constants/filters.constants';

@Component({
    templateUrl: './filters-list-selection.component.html',
    styleUrls: ['./filters-list-selection.component.scss'],
    standalone: false
})
export class FiltersListSelectionComponent extends AbstractControlBaseComponent implements AfterViewInit {
	constructor(@Optional() private readonly bottomSheetRef: MatBottomSheetRef<BaseFiltersComponent>) {
		super();
	}

	@ViewChild('buttonOptionTemplate') singleOptionRef: TemplateRef<any>;
	@ViewChild('checkboxOptionTemplate') checkboxOptionRef: TemplateRef<any>;
	@ViewChild('selectionListTemplate') selectionListRef: TemplateRef<any>;
	@ViewChild('dropdownSelectionListTemplate') dropdownSelectionRef: TemplateRef<any>;
	@ContentChild('option') optionRef: TemplateRef<any>;
	@ContentChild('optionList') optionListRef: TemplateRef<any>;

	readonly isSelectAllMapper = [FilterType.COUNTY, FilterType.COUNTY];

	onClickMultiOption(filterValue: FilterDropdownValue): void {
		this.filtersService.updateControlArrayValue(this.filter, filterValue);

		if (!this.bottomSheetRef) {
			this.filtersService.updateRoute();
		}

		this.changed.emit(this.filter);
	}

	onClickSingleOption(filterValue: FilterDropdownValue): void {
		const FORM_VALUE = FILTERS_KEY_REFERENCED.includes(this.filter.paramName) ? filterValue.reference : filterValue.id;

		this.filtersService.updateControlValue(this.filter, FORM_VALUE);

		this.onFilterChange();
	}

	onSelectAllSingleOption(): void {
		// Reset the of County Area if County value is empty or null.
		if (this.filter.paramName === FilterType.COUNTY) {
			const CHILD_FILTER = this.filtersFacade.getFilter(this.filter?.childReferenceParamName);

			this.filtersFacade.updateFilter({
				...CHILD_FILTER,
				values: undefined,
			});
		}

		this.filtersService.updateControlValue(this.filter, undefined);
		this.onFilterChange();
	}

	onSelectAllMultiOption(isSelectedAll: boolean): void {
		this.filtersService.selectAllFilterDropdownMultiOption(this.filter, this.filter.values?.all || this.filter.values, isSelectedAll);

		if (!this.bottomSheetRef) {
			this.filtersService.updateRoute();
		}

		this.changed.emit(this.filter);
	}

	/**
	 * Handle filter change
	 *
	 * • On bottom sheet, only on close event we are triggering the update or submit filters
	 * • On side Filters form, trigger immediately the update or submit filters on change
	 */
	private onFilterChange(): void {
		if (this.bottomSheetRef) {
			this.filtersFacade.setToggleProgressBar(true);
			this.bottomSheetRef.dismiss(FilterAction.UPDATE);
		} else {
			this.filtersService.updateRoute();
		}

		// If parent Filter has a child reference Filter, get the child reference Filter value
		if (this.fc[this.filter.paramName]?.value) {
			this.getChildFilterValue();
		}

		this.changed.emit(this.filter);
	}

	ngAfterViewInit(): void {
		CommonUtil.handleExpressionChangedError(() => {
			this.optionRef = this.filter.templateType === FilterTemplateType.MULTI_SELECTION ? this.checkboxOptionRef : this.singleOptionRef;
			this.optionListRef = this.bpoService.isMobile() ? this.selectionListRef : this.dropdownSelectionRef;
		});
	}
}
