import { Component, Input, OnInit } from '@angular/core';
import { AbstractControl, FormControl, FormGroup } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { FeatureVersion, FilterType } from '@core/enums';
import { Filter } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable } from 'rxjs';
import { FiltersFacade } from '../../filters.facade';
import { FiltersService } from '../../services/filters.service';
import { environment } from '@environments/environment';

@Component({
    selector: 'findqo-filters-search',
    templateUrl: './filters-search.component.html',
    styleUrls: ['./filters-search.component.scss'],
    standalone: false
})
export class FiltersSearchComponent implements OnInit {
	constructor(private readonly filtersService: FiltersService, private readonly filtersFacade: FiltersFacade) {
		this.isRangeSearchEnabled$ = this.filtersFacade.getIsRangeSearchEnabled$();
	}

	@Input() filter: Filter;
	@Input() searchControl: FormControl;
	@Input() placeholder: string;

	isRangeSearchEnabled$: Observable<boolean>;
	form: FormGroup;
	othersForm: FormGroup;
	currentRangeValue = '100';

	readonly buttonType = ButtonType;
	readonly filterType = FilterType;
	readonly featureVersion = FeatureVersion;
	readonly currentFeatureVeresion = environment.featureVersion;

	// Easy getter for the form controls
	get fc(): { [p: string]: AbstractControl } {
		return this.form.controls;
	}

	get othersFc(): { [p: string]: AbstractControl } {
		return this.othersForm.controls;
	}

	onEnableRangeSearch(): void {
		if (navigator.geolocation) {
			navigator.geolocation.getCurrentPosition(
				(position: GeolocationPosition) => {
					if (position) {
						this.filtersService.updateLocation(position);
						this.filtersFacade.setIsRangeSearchEnabled(true);
					}
				},
				(error) => {
					console.error(error);

					alert('Geolocation is not supported by this browser.');
				}
			);
		} else {
			alert('Geolocation is not supported by this browser.');
		}
	}

	onToggleRangeSearch(matCheckbox: MatCheckboxChange): void {
		if (!matCheckbox.checked) {
			this.currentRangeValue = this.fc[FilterType.RANGE].value;
			this.fc[FilterType.RANGE].patchValue(null);
		} else {
			this.fc[FilterType.RANGE].patchValue(this.currentRangeValue);
		}
	}

	private initDeclarations(): void {
		this.form = this.filtersService.getFiltersForm();
		this.othersForm = this.filtersService.getOthersForm();

		if (this.filtersFacade.getIsRangeSearchEnabled()) {
			const RANGE_CONTROL = this.fc[FilterType.RANGE];

			RANGE_CONTROL.patchValue(RANGE_CONTROL.value ? RANGE_CONTROL.value : '100');
		}
	}

	ngOnInit(): void {
		this.initDeclarations();
	}
}
