import { Component, OnInit } from '@angular/core';
import { MatSelectChange } from '@angular/material/select';
import { FilterTemplateType, FilterType } from '@core/enums';
import { RecentSearch } from '@core/models';
import { AbstractFiltersFormComponent } from '@feature/general/filters/abstracts/filters-form.abstract';
import { QuickFiltersPipe } from '@shared/pipes';
import { DesktopFilterDialogComponent } from '../../components/desktop-filter-dialog/desktop-filter-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { PropertyAlertDialogComponent } from '../../components/property-alert-dialog/property-alert-dialog.component';
import { AuthService } from '@core/data-access/services/auth.service';

@Component({
    selector: 'findqo-filters-desktop',
    templateUrl: './filters-desktop.component.html',
    styleUrls: ['./filters-desktop.component.scss'],
    providers: [QuickFiltersPipe],
    standalone: false
})
export class FiltersDesktopComponent extends AbstractFiltersFormComponent implements OnInit {
	constructor(
		private readonly quickFiltersPipe: QuickFiltersPipe,
		private readonly dialog: MatDialog,
		public authService: AuthService
	) {
		super();

		this.recentSearches$ = this.commonFacade.recentSearches$;
	}

	recentSearches$: Observable<RecentSearch[]>;

	quickFilterListCount = 0;

	readonly filterTemplateType = FilterTemplateType;
	readonly filterType = FilterType;
	get locations() {
		return this.filtersService.locations;
	}

	onMinMaxChange(): void {
		this.filtersService.updateRoute();
	}

	onAisleChange(matSelectChange: MatSelectChange): void {
		this.router.navigate([this.params['section'], matSelectChange.value]);
	}

	openFilters(): void {
		this.dialog.open(DesktopFilterDialogComponent, {
			width: '100%',
			height: '100%',
			maxWidth: '400px',
			position: {
				right: '0',
			},
			data: {
				filterList: this.filterList,
			},
		});
	}

	fetchRecentSearch(): void {
		this.commonFacade.fetchRecentSearch();
	}

	private initDeclarations(): void {
		this.fetchRecentSearch();
		this.quickFilterListCount = this.quickFiltersPipe.transform(this.filterList)?.length;
	}

	onShowAlertsDialog() {
		if (!this.authService.user()) {
			this.authService.openAuthDialog();
			return;
		}

		this.dialog.open(PropertyAlertDialogComponent, {
			disableClose: true,
			panelClass: 'property-alert-dialog',
			data: {
				section: this.route.snapshot.params['section'],
				aisle: this.route.snapshot.params['aisle'],
				location: { ...(this.filtersService.locations.value.length ? { countyReference: this.filtersService.locations.value[0] } : {}) },
			},
		});
	}

	override ngOnInit(): void {
		super.ngOnInit();
		this.initDeclarations();
	}
}
