import { Component, Input, OnChanges, OnInit, SimpleChanges, inject } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { DROPDOWN_SORT } from '@core/constants/dropdown.constant';
import { IconType } from '@core/enums/icon.enum';
import { CommonFacade } from '@core/facades/common.facade';
import { Aisle, Filter } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable } from 'rxjs';
import { FiltersFacade } from '@feature/general/filters/filters.facade';
import { FiltersService } from '../services/filters.service';
import { RouteService } from '@core/services/route.service';

@Component({
    template: '',
    standalone: false
})
export abstract class AbstractFiltersFormComponent implements OnInit, OnChanges {
	// TODO: Add more details or create README.md file

	public readonly filtersService = inject(FiltersService);
	public readonly commonFacade = inject(CommonFacade);
	public readonly filtersFacade = inject(FiltersFacade);
	public readonly routeService = inject(RouteService);
	public readonly route = inject(ActivatedRoute);
	public readonly router = inject(Router);

	// TODO: Move this to the specific components that need these @Input decorators.
	@Input() filterList: Filter[];
	@Input() aisleList: Aisle[];
	@Input() currentAisle: string;
	@Input() form!: FormGroup;

	// @Output() onChange = new EventEmitter<any>();

	selectedFilterCount$: Observable<number>;
	params: Params;

	toggleSeeResults = false;

	readonly iconType = IconType;
	readonly buttonType = ButtonType;
	readonly sortValues = DROPDOWN_SORT;

	// Easy getter for the form controls
	get fc(): { [p: string]: AbstractControl } {
		return this.form.controls;
	}

	/**
	 * Callback method when Filters Input changed the value
	 *
	 * @param control Form Control
	 */
	onFilterChanged(filter: Filter): void {
		this.filtersFacade.onControlChange(filter, this.form);
	}

	/**
	 * Display the total results in menu filter
	 */
	onGetTotalResults(): void {
		// TODO: Implement the API needed here. After filtering in the filters menu the API will be triggered to get the total results count.
		this.toggleSeeResults = true;
	}

	// TODO: Need to check if we need after the API is integrated.
	onCloseFiltersMenu(): void {
		this.toggleSeeResults = false;
	}

	trackByFilterId(_index: number, filter: Filter): string {
		return filter.paramName;
	}

	// TODO: Need to remove this
	/**
	 * Initialize the Form
	 *
	 * @param controls Dynamic controls
	 */
	private createForm(filterList: Filter[]): void {
		if (!this.filtersService.getFiltersForm()) {
			this.filtersService.createFilterForm(filterList, this.params);
			this.form = this.filtersService.getFiltersForm();
		}
	}

	/**
	 * Listens to params change
	 */
	private onParamsChange(): void {
		this.route.params.subscribe((params: Params) => (this.params = params));
	}

	private init(): void {
		this.selectedFilterCount$ = this.filtersFacade.getSelectedFilterCount$();

		// TODO: Need to remove this
		this.createForm(this.filterList);
	}

	ngOnInit(): void {
		this.init();
		this.onParamsChange();
	}

	ngOnChanges(changes: SimpleChanges): void {
		CommonUtil.handleNgOnChanges(changes['form'], (form: FormGroup) => {
			// Get the latest Filter form
			this.form = form;
		});
	}
}
